import React, { useEffect, useState } from 'react';
import Paginator from "../common/Paginator/Paginator";
import User from "./User";

let Users = ({ currentPage, totalUsersCount, pageSize, onPageChanged, users, subscriptions, changePageSize, ...props }) => {

    const onKeyPress = (event) => {
        const {key, currentTarget: {value}} = event
        if (key !== "Enter")
            return 
        if (value === "") {
            return changePageSize(currentPage, "1")
        }
        changePageSize(currentPage, value)
    }

    const onBlur = (event) => {
        const {currentTarget: {value}} = event
        if (value === "") {
            return changePageSize(currentPage, "1")
        }
        changePageSize(currentPage, value)
    }
    
    return <div>
        <Paginator currentPage={currentPage} onPageChanged={onPageChanged}
            totalUsersCount={totalUsersCount} pageSize={pageSize}
        />
        <br />
        Set page size: <input type="number" defaultValue={pageSize} onKeyPress={onKeyPress} onBlur={onBlur} />
        <div>
            {
                users.map(u => <User user={u}
                    followingInProgress={props.followingInProgress}
                    key={u.id}
                    unfollow={props.unfollow}
                    follow={props.follow}
                    subscriptions={subscriptions}
                    isAuth={props.isAuth}
                    userId={props.userId}
                />
                )
            }
        </div>
    </div>
}

export default Users;