import React, { useState } from 'react';
import s from "./Paginator.module.css";

const PORTION_SIZE = 10;

let Paginator = ({ totalUsersCount, pageSize, currentPage, onPageChanged }) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize)
    let portionsCount = Math.ceil(totalUsersCount / (pageSize * PORTION_SIZE))
    let [currentPortionNumber, setCurrentPortion] = useState(1)
    let portionsLeftBorder = (currentPortionNumber - 1) * PORTION_SIZE + 1
    let portionsRightBorder = currentPortionNumber * PORTION_SIZE

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    return <div>
        {currentPortionNumber > 1
            && <button className={s.swapPageButton} onClick={() => {setCurrentPortion(currentPortionNumber - 1)}}>Prev</button>}

        {pages.filter(p => p >= portionsLeftBorder && p <= portionsRightBorder).map(p => {
            return <span className={currentPage === p && s.selectedPage}
                onClick={(e) => {
                    onPageChanged(p);
                }}>{p} </span>
        })}

        {currentPortionNumber !== portionsCount
            && <button className={s.swapPageButton} onClick={() => {setCurrentPortion(currentPortionNumber + 1)}}>Next</button>}
    </div>
}

export default Paginator;   