import React from 'react';
import styles from "./users.module.css";
import { NavLink } from "react-router-dom";
import { onError } from '../../utils/onError';
import HOST from '../../env'

let User = ({ user, followingInProgress, unfollow, follow, subscriptions, isAuth, userId }) => {
    return (
        <div>
            <div>
                <div>
                    <NavLink to={'/profile/' + user._id}>
                        <img src={user.profile.photo}
                            onError={onError}
                            className={styles.userPhoto}
                        />
                    </NavLink>
                </div>
                {isAuth && user._id !== userId && <div>   
                    {subscriptions.some(id => id === user._id)
                        ? <button disabled={followingInProgress
                            .some(id => id === user._id)}
                            onClick={() => { unfollow(user._id) }}>
                            Unfollow</button>
                        : <button disabled={followingInProgress.some(id => id === user._id)}
                            onClick={() => { follow(user._id) }}>
                            Follow</button>}

                </div>}
                
            </div>
            <div>
                <div>
                    <div>{user.nickname}</div>
                    <div>{user.profile.fullName}</div>
                    <div>{user.status}</div>
                </div>
                <div>
                    <div>{user.profile.location.country}</div>
                    <div>{user.profile.location.city}</div>
                </div>
            </div>
        </div>)
}

export default User;